<template>
	<div class="contents">
		<h1 class="page-header">Create Post</h1>
		<div class="form-wrapper">
			<form class="form">
				<div>
					<label for="title"> Ttitle : </label>
					<input type="text" id="title" v-model="title" />
				</div>
				<div>
					<label for="contents">Contents:</label>
					<textarea id="contents" v-model="contents" rows="3" />
					<p class="valitation-text warning" v-if="!isContentsValid">
						Text is Too Long
					</p>
				</div>
				<button type="button" @click="doPost()" class="btn">
					등록
				</button>
			</form>
		</div>
	</div>
</template>

<script>
import { doPost } from '@/api/post';
export default {
	data() {
		return {
			title: '',
			contents: '',
		};
	},
	computed: {
		isContentsValid() {
			return this.contents.length < 200;
		},
	},
	methods: {
		async doPost() {
			try {
				let res = await doPost({
					title: this.title,
					contents: this.contents,
					crtId: this.$store.state.userId,
				});
				if (res.result == 0) {
					alert(res.resultMsg);
					this.$router.push('/main');
				}
			} catch (e) {
				console.log(e.response);
			}
		},
	},
};
</script>

<style scoped>
.form-wrapper .form {
	width: 100%;
}
.btn {
	color: white;
}
</style>
